import React from 'react';
import { useLocation } from 'react-router-dom';
import { YMInitializer } from 'react-yandex-metrika';

//  YOUR_COUNTER_ID   ID   
const YANDEX_METRICA_ID = '106218847';

const YandexMetrica: React.FC = () => {
  const location = useLocation();

  React.useEffect(() => {
    console.log('Yandex Metrica:   ID:', YANDEX_METRICA_ID);
  }, []);

  React.useEffect(() => {
    //    
    console.log('Yandex Metrica:  ', location.pathname);
    if (window.ym) {
      window.ym(YANDEX_METRICA_ID, 'hit', location.pathname);
      console.log('Yandex Metrica: hit  ', location.pathname);
    } else {
      console.warn('Yandex Metrica: window.ym   ');
    }
  }, [location]);

  return (
    <YMInitializer
      accounts={[Number(YANDEX_METRICA_ID)]}
      options={{
        clickmap: true,
        trackLinks: true,
        accurateTrackBounce: true,
        webvisor: true,
      }}
      version="2"
    />
  );
};

export default YandexMetrica;
